                                        <td class="text-right" style="height:34px;" >{{data.value.jun}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.jul}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.ags}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.sep}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.okt}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.nov}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.des}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.total}}</td>
