                                        <td class="text-right" style="height:34px;" >{{data.value.budget}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.variancebudget}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.varbudget}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.lastyear}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.variancelastyear}}</td>
                                        <td class="text-right" style="height:34px;" >{{data.value.varlastyear}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <!-- Table Show -->
                    <div class="table-wrapper">
                        <table>
                            <thead>
                                <tr class="bg-primary text-white">
                                    <th>NAMA</th>
                                    <th>JANUARI</th>
                                    <th>FEBRUARI</th>
                                    <th>MARET</th>
                                    <th>APRIL</th>
                                    <th>MEI</th>
                                    <th>JUNI</th>
                                    <th>JULI</th>
