                                    <th>VARIANCE</th>
                                    <th>% VAR</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(data,index) in laba" :key="index">
                                    <th :style="data.style == 'satu' ? 'padding-left:15px;  font-weight: bold;' :data.style == 'tiga' ? 'font-weight: bold;' :data.style == 'dua' ? 'padding-left:25px' :null" class="zui-sticky-col" style="height:34px;">{{data.name}}</th>
                                    <td class="text-right" style="height:34px;" >{{data.value.jan === '-' ? data.value.jan : data.value.jan.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.feb === '-' ? data.value.feb : data.value.feb.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
