                                    <td class="text-right" style="height:34px;" >{{data.value.apr === '-' ? data.value.apr : data.value.apr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.mei === '-' ? data.value.mei : data.value.mei.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.jun === '-' ? data.value.jun : data.value.jun.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
