                                    <td class="text-right" style="height:34px;" >{{data.value.nov === '-' ? data.value.nov : data.value.nov.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.des === '-' ? data.value.des : data.value.des.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.total === '-' ? data.value.total : data.value.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
