                                    <td class="text-right" style="height:34px;" >{{data.value.budget === '-' ? data.value.budget : data.value.budget.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.variancebudget === '-' ? data.value.variancebudget : data.value.variancebudget.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.varbudget === '-' ? data.value.varbudget : data.value.varbudget.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
