                                    <td class="text-right" style="height:34px;" >{{data.value.variancelastyear === '-' ? data.value.variancelastyear : data.value.variancelastyear.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                    <td class="text-right" style="height:34px;" >{{data.value.varlastyear === '-' ? data.value.varlastyear : data.value.varlastyear.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}}</td>
                                </tr>
