</template>

<script>
import Datepicker from 'vuejs-datepicker'
import vcheckboxpopup from '../components/CheckboxPopup'

export default {
    data() {
        return{
            uri :'data:application/vnd.ms-excel;base64,',
            template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
            base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
            format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
            selectedMonth:new Date(),
            laba:[],
            labaExcel:[],
            entitas:[],
            selected: [],
            popupActivo:false,
            excell: true,
            search:''
        }
    },

    components: {
        Datepicker,
        vcheckboxpopup
    },

    computed: {
        selectAll: {
            get () {
                return this.selected.length === this.entitas.length
            },
            set (value) {
                this.selected = []

                if (value) {
                this.entitas.forEach((select) => {
                    this.selected.push(select.kode)
                })
                }
            }
        },

        filteredItems() {
            return this.entitas.filter(item => {
                if(item.nama == undefined)
                return false
                return item.nama.toLowerCase().indexOf(this.search.toLowerCase()) > -1
            })
        }
    },

    mounted() {
        this.loadEntitas()
    },

    methods: {
        loadEntitas() {
            this.entitas = this.$store.state.AppActiveUser.entitas
        },

        tableToExcel(table, name) {
            if (!table.nodeType) table = this.$refs.table
            var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            window.location.href = this.uri + this.base64(this.format(this.template, ctx))
        },

        loadDataServer() {
            if (this.selected.length == 0){
                this.$swal.fire(
                    'Oopss !',
                    'Anda Belum Memilih Entitas',
                    'error'
                )
            } else {
                this.$vs.loading()
                let bulanPost =  btoa(this.selectedMonth);
                let entitasPost = {
                    kode_entitas : this.selected
                }

                this.$http.post('neraca/rlkonsolidasi/' + bulanPost, entitasPost)
                .then(res => {
                    this.laba = res.data.message
                    this.labaExcel = res.data.message2
                    this.excell = false
                    })
                .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                    )
                })
                .finally(() => {this.$vs.loading.close()})
            }
        },

        setMargin(items) {
            return `margin-left: ${items.id == 2 * 20}px;`;
        }

    }
}
</script>

<style scoped>
.table-wrapper{
  overflow-y: scroll;
  height: 560px;
}

table {
  position: relative;
  border-collapse: collapse;
}

td,
th {
    padding: 10px;
    white-space: nowrap;
    border: solid 1px #DDEFEF;
}

thead th {
    position: -webkit-sticky; /* for Safari */
    position: sticky;
    top: 0;
    background: #7367f0;
    text-align: center;
    color: #fff;
    border: solid 1px #DDEFEF;
    padding: 10px;
    white-space: nowrap;
    width: 363px;
}

thead th:first-child {
  left: 0;
  z-index: 1;
}

tbody th {
    position: -webkit-sticky; /* for Safari */
    position: sticky;
    left: 0;
    padding: 10px;
    white-space: nowrap;
    border: solid 1px #DDEFEF;
    background: #fff;
    color: #797777;
    text-align:left;
}
</style>